$green:#459D36;
$gray:#dedfe0;
$hui:rgb(133, 133, 133);
* {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
}

a {
    color: $green;
}

body,
html {
    height: 100%;
}

body {
    background: #f7f7f7;
}

main {
    width: 550px;
    height: 500px;
    background: #fff;
    margin: 100px auto 40px;
    // overflow: hidden;
    border-radius: 40px;
    box-shadow: 0px 5px 5px 5px #e7e7e7;
    position: relative;
}

#logo {
    position: absolute;
    width: 130px;
    height: 42px;
    margin-left: 185px;
    margin-top: 20px;
    img {
        width: 100%;
    }
}

.box {
    width: 300px;
    position: absolute;
    top: 120px;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    legend {
        font-size: 24px;
        font-weight: 900;
        color: $hui;
        margin-left: 100px;
    }
    form {
        margin-top: 10px;
        p {
            color: $hui;
            margin-top: 10px;
            input:nth-of-type(1),
            input[type="submit"] {
                height: 46px;
                width: 300px;
                border: 1px solid #dedfe0;
                border-radius: 10px;
            }
            input:nth-of-type(1) {
                margin-top: 10px;
                text-indent: 12px;
                font-size: 16px;
                color: #252525;
                line-height: 46px;
            }
            input[type="submit"] {
                margin-top: 30px;
                background: #459D36;
                text-indent: 12px;
                color: #fff;
                font-size: 17px;
                cursor: pointer;
            }
        }
    }
}

.yhmts {
    display: none;
    width: 350px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: rgb(126, 126, 126);
    // background: rgb(233, 233, 233);
    border-radius: 3px;
}

.mits {
    display: none;
    width: 200px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: rgb(126, 126, 126);
    // background: rgb(233, 233, 233);
    border-radius: 3px;
}

.pasp {
    position: relative;
}

.yonghu {
    position: relative;
}

.mima {
    position: absolute;
    left: 280px;
    top: 25px;
    cursor: pointer;
}

.del {
    position: absolute;
    left: 280px;
    top: 25px;
    cursor: pointer;
}

footer {
    text-align: center;
    a {
        margin-right: 40px;
        font-size: 11px;
    }
    span {
        color: #666;
        font-size: 11px;
    }
    p {
        padding: 10px 0;
        font-size: 10px;
        box-sizing: border-box;
        color: #9d9d9d;
    }
}